.header {
	padding: 16px;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	background: rgba(28, 28, 33, 1);
}

.headerMenu {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.headerMenuGroup {
	display: flex;
	justify-content: flex-start;
	gap: 28px;
}

.headerMenuItem {
	flex: 1;
	display: flex;
	justify-content: space-around;
}

.headerMenuItem:first-child {
	justify-content: start;
}

.headerMenuItem:last-child {
	justify-content: end;
}

.menuItem {
	padding: 17px 20px;
	display: flex;
	gap: 8px;
	align-items: center;
	color: var(--text-inactive-color);
	font-size: 16px;
	line-height: 1.5;
	font-weight: 400;
	text-wrap: nowrap;
	text-decoration: none;
}

.activeMenuItem {
	composes: menuItem;
	color: rgba(242, 242, 243, 1);
}
