.list {
	padding: 0;
	list-style-type: none;
}

.group {
	composes: list;
	height: calc(100% - 96px);
	display: flex;
	flex-direction: column;
}

.gropItem {
	max-height: calc(100% - 192px);
}

.footer {
	padding: 20px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}
